.person-card {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary-color);
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 10px;
  transition: all 0.3s ease;
  cursor: pointer;

  &__input-label {
    font-size: small;
    font-weight: bold;
    color: var(--black);
  }

  &__input {
    border-radius: 5px;
    border-style: solid;
    border-color: var(--accent-color);
    color: var(--black);
    padding: 5px;
    font-weight: bold;

    &--name {
      width: 80px;
    }

    &--contribution {
      width: 50px;
    }
  }
}
